import Vue from 'vue'
import Router from 'vue-router'
import header from '~/components/header.vue'

const home = (resolve) => {
  import('~/components/home.vue').then((module) => {
    resolve(module)
  })
}

const office = (resolve) => {
  import('~/components/office.vue').then((module) => {
    resolve(module)
  })
}

const about = (resolve) => {
  import('~/components/about.vue').then((module) => {
    resolve(module)
  })
}

const illness = (resolve) => {
  import('~/components/illness.vue').then((module) => {
    resolve(module)
  })
}

const story = (resolve) => {
  import('~/components/story.vue').then((module) => {
    resolve(module)
  })
}

const news = (resolve) => {
  import('~/components/news.vue').then((module) => {
    resolve(module)
  })
}

Vue.use(Router)

export default new Router({
  routes: [{
    path: '/',
    component: header,
    children: [{
        path: '/',
        component: home
      },
      {
        path: '/office',
        component: office
      },
      {
        path: '/about',
        component: about
      },
      {
        path: '/illness/:id',
        name:'illness',
        component: illness
      },
      {
        path: '/story',
        name: 'story',
        component: story
      },
      {
        path: '/news',
        name: 'news',
        component: news
      },
    ]
  }]
})


