<template>
<div class="home">
  <div class="header-top" :class="currentView">
    <div class="top">
      <div class="content">
        <p>tel : 176-1070-5501</p>
      </div>
    </div>
    <div class="phone">
      <i class="el-icon-menu" @click="menu"></i>
    </div>
    <div class="bottom">
      <div class="content">
        <ul>
          <li @click="alink">
            <router-link to="/#"></router-link>
          </li>
          <li @click="alink">
            <router-link to="/about"></router-link>
          </li>
          <li @click="alink">
            <router-link to="/office"></router-link>
          </li>
          <li @click="alink">
            <router-link to="/story"></router-link>
          </li>
          <li @click="alink">
            <router-link to="/news"></router-link>
          </li>
        </ul>
      </div>
    </div>
  </div>
  <div class="main">
    <transition name="el-fade-in-linear">
      <router-view></router-view>
    </transition>
  </div>
  
  <div class="home-9 home-m">
      <div class="end">
        <ul>
            <li>
                <a href="">github</a>
            </li>
            <li>
                <a href="">gitbook</a>
            </li>
            <li>
                <a href="">facebook</a>
            </li>
            </ul>
            <img src="http://ooi407n8x.bkt.clouddn.com/an.jpg" alt="">
            <ul>
            <li>
                <a href="">weixin</a>
            </li>
            <li>
                <a href="">weibo</a>
            </li>
            <li>
                <a href="">dingding</a>
            </li>
        </ul>
      </div>
  </div>
</div>
</template>

<script>
export default {
  data() {
    return {
      currentView: ""
    };
  },
  methods: {
    menu() {
      if (this.currentView == "active") {
        this.currentView = "";
      } else {
        this.currentView = "active";
      }
    },
    alink() {
      this.currentView = "";
    }
  },
  created: function() {
    window.scrollTo(0, 0);
  },
};
</script>

<style lang="scss" scoped>
@media (max-width: 990px) {
  .header-top {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    .content {
      padding: 0 10px;
    }
    .top {
      display: none;
    }
    .phone {
      line-height: 50px;
      padding: 0 15px;
      text-align: right;
      background: #03a57b;
      color: #fff;
      position: relative;
      z-index: 10;
      i {
        font-size: 17px;
      }
    }
    img {
      width: 50px;
      border-radius: 50%;
    }
    ul {
      transform: translate(0, -120%);
      position: fixed;
      width: 100%;
      height: 100vh;
      top: 50px;
      left: 0;
      z-index: 9;
      transition: 0.6s;
      background: #03a57b;
      li:first-child {
        line-height: 40px;
      }
      li {
        text-align: center;
        a {
          line-height: 50px;
          display: block;
          transition: 0.3s;
          color: #fff;
        }
      }
    }
  }
  .header-top.active {
    .bottom {
      display: block;
      img {
        width: 50px;
        border-radius: 50%;
      }
      ul {
        transform: translate(0, 0);
      }
    }
  }
  .main {
    margin-top: 50px;
  }

  .home-9 {
    background: #333;
    padding: 30px 0;
    text-align: center;
    .end {
      letter-spacing: 2px;
      a {
        color: #fff;
      }
      img {
        width: 60px;
        border-radius: 50%;
        margin: 30px 0;
      }
      ul {
        li {
          line-height: 30px;
          width: 32%;
          display: inline-block;
        }
      }
      ul:nth-child(1) {
        display: none;
      }
    }
  }
}
@media (min-width: 992px) {
  .header-top {
    box-shadow: 0 17px 50px 0 rgba(0, 0, 0, 0.02);
    .content {
      width: 1200px;
      margin: 0 auto;
    }
    .phone {
      display: none;
    }
    .top {
      background: #03a57b;
      color: #fff;
      .content {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        p {
          line-height: 25px;
        }
      }
    }

    .bottom {
      background: #fff;
      padding-top: 10px;
      img {
        width: 60px;
        border-radius: 50%;
      }
      ul:after {
        content: "\0020";
        display: block;
        height: 0;
        clear: both;
      }
      ul {
        // li:nth-child(1) a.router-link-active {
        //   border-bottom: 0;
        // }
        .router-link-active {
          color: #03a57b;
          font-weight: bold;
          border-bottom: 2px solid #03a57b;
        }
        li {
          float: right;
          opacity: 0.8;
          a {
            line-height: 60px;
            padding: 0 20px;
            display: block;
            transition: 0.3s;
            &:hover {
              color: #03a57b;
              font-weight: bold;
            }
          }
        }
      }
    }
  }
  .home-9 {
    background: #333;
    text-align: center;
    padding: 30px 0;
    .end {
      width: 1200px;
      margin: 0 auto;
      letter-spacing: 2px;
      a {
        color: #fff;
      }
      img {
        width: 40px;
        border-radius: 50%;
      }
      ul {
        text-align: center;
        width: 30%;
        float: left;
        li {
          width: 33.3%;
          float: left;
          line-height: 40px;
        }
      }
      ul:nth-child(3) {
        width: 30%;
        float: right;
      }
    }
  }
}
</style>



